import { I18n } from 'react-redux-i18n';
import * as Yup from 'yup';

export const stringIsRequired = () => Yup.string()
  .nullable()
  .required(I18n.t('form.required'));

export const numberIsRequired = () => Yup.number()
  .nullable()
  .required(I18n.t('form.required'));

export const mrkOrganization = () => Yup.object().shape({
  name: stringIsRequired(),
  edrpo: stringIsRequired(),
  adress: stringIsRequired(),
  regAdress: stringIsRequired(),
});

export const mrkClient = () => Yup.object().shape({
  login: stringIsRequired(),
  firstName: stringIsRequired(),
  lastName: stringIsRequired(),
  middleName: stringIsRequired(),
  position: stringIsRequired(),
  //inn: stringIsRequired(),
  contacts: Yup.array().min(2).of(
    Yup.object().shape({
      cType: Yup.number().nullable().required(I18n.t('form.required')),
      cValue: Yup.mixed().when('cType', {
        is: val => {
          return val === MrkContactType.PHONE;
        },
        then: Yup.string().nullable().required(I18n.t('form.required')).matches(/^[\d-+]+$/, I18n.t('form.numbers')),
        otherwise: Yup.string().nullable().email(I18n.t('form.email')).required(I18n.t('form.required'))
      })
    })
  )
});

export const mrkAccount = () => Yup.object().shape({
  organization: mrkOrganization().default(null).nullable(),
  clientList: Yup.array().of(mrkClient())
});

export const mrkAlmexSysUser = () => Yup.object().shape({
  login: stringIsRequired(),
  password: Yup.string().when('id', (id) => id === null ? stringIsRequired() : Yup.string().nullable()),
  usePriority: numberIsRequired()
    .min(0, I18n.t('form.min', { min: 0 }))
    .max(999, I18n.t('form.max', { max: 999 }))
});

export const mrkUser = () => Yup.object().shape({
  login: stringIsRequired(),
  password: Yup.string().when('id', (id) => id === null ? stringIsRequired() : Yup.string().nullable()),
  firstName: stringIsRequired(),
  lastName: stringIsRequired(),
  middleName: stringIsRequired()
});